import { projectId, publicAnonKey } from './supabase/info';

/**
 * üé¨ Initialise les Hero Slides dynamiques dans Supabase
 * 
 * Cette fonction cr√©e 7 slides Hero avec 3 vid√©os et 4 images :
 * 
 * SLIDES CR√â√âS :
 * 1. FIMA Couchage - Image (Literie premium)
 * 2. Visite Showroom - VID√âO (D√©couverte des espaces)
 * 3. FIMA Design - Image (Menuiserie sur-mesure)
 * 4. Savoir-faire Artisanal - VID√âO (Fabrication)
 * 5. UNIVERS GLASS - Image (Vitrerie & Aluminium)
 * 6. Grands Projets - VID√âO (H√¥tellerie de luxe)
 * 7. Groupe FIMA - Image (Pr√©sentation groupe)
 * 
 * CARACT√âRISTIQUES VID√âOS :
 * - Support multilingue (FR/EN)
 * - Dur√©e personnalis√©e (10-15 secondes)
 - Loop configurable
 * - Fallback sur image si vid√©o non disponible
 * - Auto-play avec mute
 * 
 * @returns Promise avec le r√©sultat de l'initialisation
 */
export async function initHeroSlides(): Promise<{
  success: boolean;
  message?: string;
  data?: any;
  error?: string;
}> {
  try {
    console.log('üé¨ Initialisation des Hero Slides...');
    
    const response = await fetch(
      `https://${projectId}.supabase.co/functions/v1/make-server-98c6ec1c/api/init-hero-slides`,
      {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${publicAnonKey}`,
          'Content-Type': 'application/json',
        },
      }
    );

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result = await response.json();

    if (result.success) {
      console.log('‚úÖ Hero Slides initialis√©s avec succ√®s !');
      console.log(`üìä Total: ${result.data.total_slides} slides`);
      console.log(`üé• Vid√©os: ${result.data.video_slides}`);
      console.log(`üñºÔ∏è Images: ${result.data.image_slides}`);
      console.log('üìã D√©tails:', result.data.slides);
    }

    return result;
  } catch (error) {
    console.error('‚ùå Erreur lors de l\'initialisation des Hero Slides:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error'
    };
  }
}

/**
 * üìã R√©cup√®re tous les Hero Slides depuis Supabase
 * 
 * @param locale - Langue souhait√©e ('fr' ou 'en')
 * @returns Promise avec la liste des slides
 */
export async function getHeroSlides(locale: 'fr' | 'en' = 'fr'): Promise<{
  success: boolean;
  data?: any[];
  error?: string;
}> {
  try {
    const response = await fetch(
      `https://${projectId}.supabase.co/functions/v1/make-server-98c6ec1c/api/hero-slides?locale=${locale}`,
      {
        headers: {
          'Authorization': `Bearer ${publicAnonKey}`,
          'Content-Type': 'application/json',
        },
      }
    );

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const result = await response.json();
    return result;
  } catch (error) {
    console.error('‚ùå Erreur lors de la r√©cup√©ration des Hero Slides:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error'
    };
  }
}

/**
 * üéØ V√©rifie si des Hero Slides existent d√©j√†
 * 
 * @returns Promise<boolean> - true si des slides existent
 */
export async function checkHeroSlidesExist(): Promise<boolean> {
  try {
    const result = await getHeroSlides();
    return result.success && result.data && result.data.length > 0;
  } catch (error) {
    console.error('Erreur lors de la v√©rification des slides:', error);
    return false;
  }
}

